.patios-page{font-family:"Hind",sans-serif}.patios-page__banner{min-height:400px;background:linear-gradient(135deg, #3B6F7F 0%, rgb(42.8225806452, 80.564516129, 92.1774193548) 100%);display:flex;align-items:center;justify-content:center}.patios-page__banner-overlay{width:100%;padding:6rem 0;text-align:center;color:#fff}.patios-page__banner-title{font-family:"Outfit",sans-serif;font-size:2.25rem;font-weight:700;margin-bottom:1.5rem}@media(min-width: 768px){.patios-page__banner-title{font-size:3rem}}.patios-page__banner-subtitle{font-size:1.125rem;max-width:700px;margin:0 auto;opacity:.95}.patios-page__section-title{font-family:"Outfit",sans-serif;font-size:1.875rem;font-weight:700;color:#3b6f7f;margin-bottom:2rem}.patios-page__caracteristicas{padding:6rem 0;background-color:#fff}.patios-page__caracteristica{text-align:center;padding:2rem;background-color:#f5f5f5;border-radius:12px;height:100%;transition:all .3s ease}.patios-page__caracteristica:hover{transform:translateY(-5px);box-shadow:0 4px 8px rgba(0,0,0,.15)}.patios-page__caracteristica-icono{font-size:3rem;margin-bottom:1.5rem}.patios-page__caracteristica-titulo{font-family:"Outfit",sans-serif;font-size:1.25rem;font-weight:700;color:#3b6f7f;margin-bottom:1rem}.patios-page__caracteristica-descripcion{color:#333;font-size:1rem;line-height:1.6}.patios-page__patio{padding:6rem 0;background-color:#f5f5f5}.patios-page__patio--alt{background-color:#fff}.patios-page__patio-card{border:none;border-radius:12px;box-shadow:0 4px 8px rgba(0,0,0,.15);height:100%}.patios-page__patio-nombre{font-family:"Outfit",sans-serif;font-size:1.5rem;font-weight:700;color:#3b6f7f;margin-bottom:2rem}.patios-page__patio-info{display:flex;flex-direction:column;gap:1.5rem}.patios-page__patio-item strong{font-family:"Outfit",sans-serif;color:#3b6f7f;font-weight:600;display:block;margin-bottom:.5rem}.patios-page__patio-item p{color:#333;line-height:1.6;margin:0}.patios-page__servicios-list{list-style:none;padding-left:0;margin:0}.patios-page__servicios-list li{color:#333;padding:.25rem 0;position:relative;padding-left:1.5rem}.patios-page__servicios-list li:before{content:"✓";position:absolute;left:0;color:#f16700;font-weight:700}.patios-page__mapa{border-radius:12px;overflow:hidden;box-shadow:0 4px 8px rgba(0,0,0,.15);height:450px}.patios-page__mapa-placeholder{height:100%;background-color:#ccc;display:flex;align-items:center;justify-content:center;border-radius:12px}.patios-page__mapa-placeholder p{color:#fff;font-size:1.125rem;font-weight:600}.patios-page__cta{padding:6rem 0;background:linear-gradient(135deg, #3B6F7F 0%, rgb(42.8225806452, 80.564516129, 92.1774193548) 100%);color:#fff}.patios-page__cta-title{font-family:"Outfit",sans-serif;font-size:1.875rem;font-weight:700;margin-bottom:1.5rem}.patios-page__cta-text{font-size:1.125rem;line-height:1.8;margin-bottom:2rem;opacity:.95}.patios-page__cta-button{display:inline-block;padding:1rem 3rem;background-color:#f16700;color:#fff;font-size:1.125rem;font-weight:700;text-decoration:none;border-radius:8px;transition:all .3s ease;box-shadow:0 4px 8px rgba(0,0,0,.15)}.patios-page__cta-button:hover{background-color:rgb(190,81.2033195021,0);transform:translateY(-3px);box-shadow:0 8px 16px rgba(0,0,0,.2);color:#fff}